@import '@components/nav/nav.scss';
@import '@components/button/button.scss';
@import '@src/assets/styles/mixins.scss';

.header {
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    align-items: center;
    height: 70px;
    background-color: $color-white;
    border-radius: 20px;
    padding: 0 24px;
    z-index: 10;

    @include mobile-and-tablet {
        padding: 0 10px;
    }

    &__phone {
        font-weight: 700;
        margin-left: 40px;
        cursor: pointer;
        text-decoration: none;

        @include tablet {
            margin-left: 0;
        }
    }

    &__logo-wrapper {
        display: flex;
        flex-grow: 1;

        @include mobile-and-tablet {
            svg {
                width: 126px;
                height: 14px;
            }
        }
    }

    &__buttons {
        display: flex;
        column-gap: 10px;
    }

    &__controll {
        display: flex;
        align-items: center;
        column-gap: 18px;

        @include tablet {
            column-gap: 10px;

            .nav {
                column-gap: 20px;
            }
        }

        @include mobile {
            display: none;
        }
    }

    &__burger {
        display: none;

        @include mobile {
            display: block;
        }
    }
}

.header__button.hidden {
    display: none;
}