.burger-modal.hidden {
    display: none;
}

.burger-modal {
    @include modal-hide {
        display: none !important;
    }

    &__wrapper {
        width: 100%;
        position: fixed;
        top:  80px;
        left: 0;
        z-index: 10;
    }

    &__backdrop {
        position: absolute;
        z-index: 8;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        background-color: $color-black;
        opacity: 0.6;
    }

    &__content {
        background-color: $color-white;
        padding: 10px 20px;
        border-radius: 20px;
        margin-bottom: 10px;
    }

    &__bottom {
        border-radius: 20px;
        padding: 20px;
        background-color: $color-white;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__bottom-links {
        display: flex;
        flex-direction: column;
        row-gap: 10px;
    }

    &__phone {
        @include body;

        text-decoration: none
    }

    &__mail {
        @include body-2;

        text-decoration: underline;
        color: $color-red;
    }
}

.burger-modal.hidden {
    display: none;
}