.nav {
    display: flex;
    list-style: none;
    column-gap: 40px ;

    &__item {
        display: flex;
        align-items: center;
        cursor: pointer;
    }

    &--vertical {
        flex-direction: column;

        .nav__item {
            justify-content: space-between;
            padding: 4px;

            &:not(:last-child) {
                border-bottom: 1px solid $color-sand-brown;
            }
        }

        .nav__link {
            display: flex;
            align-items: center;
            height: 42px;
        }
    }
}